<template>
  <div id="app">
    <LocaleSwitcher />
    <transition @leave="leave">
      <Welcome v-if="show && $route.name === 'Home'" :dataReady="dataReady" />
    </transition>

    <!-- <TransitBlock /> -->
    <!-- <div ref="transitBlock" class="transitBlock"></div>
    <div ref="transitBlock2" class="transitBlock2"></div> -->
    <main v-if="dataReady">
      <router-view />
    </main>
  </div>
</template>

<script>
import LocaleSwitcher from '@/components/LocaleSwitcher.vue'
import Welcome from '@/components/Welcome.vue'
// import TransitBlock from '@/components/TransitionBlock.vue'
import isMobile from 'ismobilejs'
import { bus } from './main'

export default {
  name: 'master',
  data () {
    return {
      show: false,
      leaveShit: true
    }
  },
  components: {
    LocaleSwitcher,
    Welcome
    // TransitBlock
  },
  computed: {
    dataReady () {
      if (
        this.$store.state.data.allProjectsLoaded &&
        this.$store.state.data.allPagesLoaded &&
        this.$store.state.data.allProjectsTWLoaded &&
        this.$store.state.data.allPackagesLoaded
      ) {
        return true
      }
      return false
    }
  },
  methods: {
    getVH () {
      // First we get the viewport height and we multiple it by 1% to get a value for a vh unit
      const vh = window.innerHeight * 0.01
      // Then we set the value in the --vh custom property to the root of the document
      document.documentElement.style.setProperty('--vh', `${vh}px`)
    },

    leave (el, done) {
      // const targets = [this.$refs.transitBlock, this.$refs.transitBlock2]
      // console.log(el)
      this.$anime({
        targets: el,
        easing: 'easeOutExpo',
        // width: ['100%', 0],
        opacity: [1, 0],
        duration: 3000
      })

      // this.$anime({
      //   targets,
      //   translateX: ['100%', '-100%'],
      //   // opacity: [0, 1, 0],
      //   easing: 'easeOutExpo',
      //   delay: this.$anime.stagger(100),
      //   duration: 2000,
      //   begin: function (anim) {
      //     bus.$emit('launchSlide')
      //   }
      //   // }
      //   // complete: () => {
      //   //   EventBus.$emit('finished')
      //   // }
      // })
    }
  },
  mounted () {
    bus.$on('enter', () => {
      this.show = false
    })

    this.getVH()

    const userAgent = window.navigator.userAgent
    if (!isMobile(userAgent).any) {
      window.addEventListener('resize', () => {
        this.getVH()
      })
    }
  }
}
</script>

