<template>
  <div class="locale-switcher">
    <div class="icon-wrapper">
      <div class="mail-icon">
        <a href="mailto:info@redspotinteriordesign.ca">
          <img
            src="@/assets/img/envelope-regular.svg"
            alt="envelope-regular.svg"
          />
        </a>
      </div>

      <div class="switch-btn-wrapper">
        <div
          class="switch-btn"
          v-for="(locale, i) in locales"
          :key="locale"
          @click="switchLocale(locale)"
        >
          <transition @enter="enter">
            <span ref="langText" v-if="$i18n.locale !== locale">
              {{ lang[i] }}
            </span>
          </transition>
        </div>
      </div>
    </div>
  </div>

  <!-- <div class="local-btn" @click="switchLocale(targetLang)">
    {{ targetLang }}
  </div> -->
</template>

<script>
export default {
  name: 'LocaleSwitcher',

  data () {
    return {
      locales: process.env.VUE_APP_I18N_SUPPORTED_LOCALE.split(','),
      lang: ['EN', '中文']
    }
  },
  methods: {
    switchLocale (locale) {
      if (this.$i18n.locale !== 'us') {
        this.$i18n.locale = locale

        const getHTMLTag = document.documentElement
        getHTMLTag.setAttribute('lang', this.$i18n.locale)
      }
    },

    bEnter () {
      this.$anime({
        targets: this.$refs.langText,
        translateY: 0
      })
    },

    enter () {
      this.$anime({
        targets: this.$refs.langText,
        translateY: [10, 0],
        opacity: [0, 1],
        easing: 'easeOutSine',
        duration: 500
      })
    },

    leave () {
      this.$anime({
        targets: this.$refs.langText,
        translateY: [0, -10],
        opacity: [1, 0]
      })
    }

    // switchLocale (targetLang) {
    //   if (targetLang === 'ENG') {
    //     this.$i18n.locale = 'us'
    //   } else {
    //     this.$i18n.locale = 'zh'
    //   }
    // }
  },
  computed: {
    targetLang () {
      if (this.$i18n.locale === 'us') {
        return this.lang[0]
      } else {
        return this.lang[1]
      }
    }
  }
}
</script>

<style lang="scss">
.locale-switcher {
  position: fixed;
  top:0;
  left: 0;
  height: 100%;
  width: 100%;
  pointer-events: none;
  z-index: 100;
  &:hover {
    .switch-btn {
      background: lighten($color: #94362f, $amount: 10%);
    }
  }
}
.icon-wrapper {
  display: flex;
  position: absolute;
  right: 25px;
  bottom: 23px;
  .mail-icon {
    pointer-events: all;
    margin-right: 10px;
    height: 33px;
    width: 47px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #ccc;
    border-radius: 15px;
    img {
      object-fit: contain;
      width: 25px;
    }
  }

  .switch-btn-wrapper {
    position: relative;
    height: 33px;
    width: 47px;
    .switch-btn {
      pointer-events: all;

      // bottom: 25px;
      // right: 35px;
      top: 0;
      left: 0;
      position: absolute;
      transition: all 0.5s ease;
      cursor: pointer;
      height: 100%;
      width: 100%;
      border-radius: 15px;
      background: #ccc;
      // border: 1px solid $red;
      display: flex;
      justify-content: center;
      align-items: center;

      span {
        color: #000;
        // padding: 1rem;
        position: relative;
        z-index: 10;
      }
    }
  }
}
</style>
