<script>
import { bus } from '../main'

export default {
  name: 'Welcome',
  props: ['dataReady'],
  data () {
    return {
      ready: false,
      loaded: false,
      allSiteDataLoaded: false,
      intro: '即使再小的個體, 也值得擁有最好的生活'
    }
  },
  methods: {
    enterSite () {
      bus.$emit('enter')
    },
    logoAnimate () {
      this.loaded = true
      const targets = document.querySelectorAll('path')
      const logoText = document.querySelectorAll('.logoText g')

      var textWrapper = this.$refs.slogon
      textWrapper.innerHTML = textWrapper.textContent.replace(
        /\S/g,
        "<span class='letter'>$&</span>"
      )
      this.$anime
        .timeline()
        .add({
          targets,
          rotate: ['180deg', '0'],
          easing: 'easeInOutCirc',
          opacity: [0, 1],
          duration: 800,
          scale: [2, 1],
          delay: function (el, i) {
            return 1000 + i * 100
          }
        })
        .add(
          {
            targets: logoText,
            translateX: [150, 0],
            translateZ: 0,
            opacity: [0, 1],
            easing: 'easeOutExpo',
            duration: 1800,
            delay: (el, i) => 500 + 30 * i
          },
          '-=800'
        )
        .add(
          {
            targets: 'text.cls-7',
            opacity: [0, 1],
            duration: 1800
          },
          '-=1600'
        )
        .add({
          targets,
          rotate: ['0', '180deg'],
          easing: 'easeInOutCirc',
          opacity: [1, 0],
          duration: 800,
          scale: [1, 2],
          delay: function (el, i) {
            return i * 100
          }
        })
        .add(
          {
            targets: logoText,
            translateX: [0, -100],
            translateZ: 0,
            opacity: [1, 0],
            easing: 'easeOutExpo',
            duration: 1800,
            delay: (el, i) => 500 + 30 * i
          },
          '-=800'
        )
        .add(
          {
            targets: 'text.cls-7',
            opacity: [1, 0],
            duration: 1800
          },
          '-=1600'
        )
        .add(
          {
            targets: '.letter',
            translateX: [30, 0],
            opacity: [0, 1],
            easing: 'easeOutExpo',
            duration: 1800,
            delay: (el, i) => 700 + 30 * i
          },
          '-=1200'
        )
        .add({
          targets: '.letter',
          translateX: [0, -30],
          opacity: [1, 0],
          easing: 'easeOutExpo',
          duration: 2200,
          delay: (el, i) => 500 + 40 * i,
          // complete: () => {
          //   this.enterSite()
          // },
          update: anim => {
            if (anim.progress >= 50) return this.enterSite()
          }
        })
    },
    getIntroAr () {
      return this.intro.split('')
    }
  },
  mounted () {
    window.addEventListener('load', () => {
      this.allSiteDataLoaded = true
    })
  }
}
</script>

<style lang="scss">
