  }
  .cls-6 {
    font-size: 118.08px;
  }
  .cls-7 {
    font-size: 36px;
    fill: #7b5431;
    font-family: TwCenMT-Regular, Tw Cen MT;
  }
  .cls-8 {
    letter-spacing: 0.01em;
  }

  #logo {
    transform: translate(-386px, -311px);
  }

  svg {
    width: 200px;
    overflow: inherit !important;
  }

  path {
    transform-origin: 85% 388px;
    opacity: 0;
  }

  .logoText g,
  .cls-7 {
